.event-cards-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 42%;
  height: 420px;
}
.event-cards-container .event-card-reverse,
.event-cards-container .event-card-reverse-anim {
  transform-origin: 80% 50%;
  background-image: url(../../img/event-cards/cardreverse.png);
  background-size: cover;
  width: 226px;
  height: 162px;
  margin-top: 40px;
  border-radius: 10px;
}
.event-cards-container .event-card-reverse {
  position: relative;
}
.event-cards-container .event-card-reverse-anim {
  margin-top: 0;
  position: relative;
}
.event-cards-container .card-reverse-animation {
  animation: cardReverseAnim 0.2s linear;
}
@keyframes cardReverseAnim {
  0% {
    right: 0;
    bottom: 0;
    transform: rotateX(0deg);
  }
  100% {
    right: 200px;
    bottom: 80px;
    transform: rotateX(90deg);
  }
}
.event-cards-container #card2 {
  position: absolute;
  background-size: cover;
  width: 226px;
  height: 162px;
  bottom: -202px;
  border-radius: 10px;
}
.event-cards-container .event-card-2-animation {
  animation: cardEventAnim 2s ease-out;
}
@keyframes cardEventAnim {
  0% {
    bottom: -202px;
    right: 0px;
    transform: rotateX(-180deg);
  }
  10% {
    width: 100%;
    height: 100%;
    bottom: -122px;
    right: 200px;
    transform: rotateX(-90deg);
  }
  35% {
    width: 180%;
    height: 180%;
    bottom: -50px;
    right: 390px;
    transform: rotateX(0deg);
  }
  80% {
    width: 180%;
    height: 180%;
    bottom: -50px;
    right: 390px;
  }
  100% {
    width: 100%;
    height: 100%;
    right: 0;
    bottom: -20px;
  }
}
.event-cards-container .event-card-container {
  position: relative;
  width: 226px;
  height: 162px;
}
.event-cards-container .event-card-container .event-card-1 {
  position: relative;
  transform-origin: 80% 50%;
  background-size: cover;
  width: 226px;
  height: 162px;
  border-radius: 10px;
  transition: 0.1s size, 0.1s transform;
  margin-top: 20px;
  cursor: pointer;
}
.event-cards-container .event-card-1:active {
  size: 200%;
  transform: scale(240%, 240%);
  z-index: 1;
}