#sasuage-and-redbull-roll {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 0;
  height: 0;
  background-color: #fff;
  border-radius: 15px;
  top: 250px;
  margin-left: 220px;
  overflow: hidden;
  text-align: center;
  z-index: 10;
}
#sasuage-and-redbull-roll h1 {
  font-size: 20px;
  font-weight: bold;
  margin-top: 10px;
}
#sasuage-and-redbull-roll h2 {
  font-size: 18px;
  margin-top: 20px;
}
#sasuage-and-redbull-roll p {
  font-size: 15px;
  font-style: italic;
  margin-top: 7px;
}
#sasuage-and-redbull-roll span {
  font-size: 15px;
  margin-top: 7px;
}
#sasuage-and-redbull-roll img {
  width: 100px;
}

#sasuage-and-redbull-roll .redbull-img {
  width: 60px;
}
#sasuage-and-redbull-roll .sasuages-img {
  width: 80px;
}

.redbull-sasuages-anim {
  animation: redbull-sasuages 2.8s ease-in-out;
  animation-fill-mode: forwards;
  border: 3px solid black;
}

@keyframes redbull-sasuages {
  0% {
    width: 0;
    height: 0;
    rotate: -800deg;
    opacity: 0%;
  }
  10% {
    width: 250px;
    height: 350px;
    rotate: 0deg;
    opacity: 100%;
  }
  90% {
    margin-right: 0;
    width: 250px;
    height: 350px;
    opacity: 100%;
  }
  100% {
    margin-right: 700;
    width: 0;
    height: 0;
    opacity: 0%;
  }
}